import React from 'react'

const ENDPOINT = 'http://gucitex.com.mx/test'
//const ENDPOINT2 = 'http://gucitex.com.mx/web-damaxboutique/backend/index.php?fn=getAllCategories'

async function processResponse(pro) {
  //console.log('promise:', pro)

  let data = await pro
    .then(res => res.text())
    .then(txt => {
      console.log('resTxt:', txt)
      return JSON.parse(txt)
    })
    .catch(error => {
      //console.log('error fetch:', error)
      alert('ERROR DE COMUNICACION CON EL SERVIDOR')
      return null
    })

  console.log('data before return:', data)

  if (data !== null) {
    //data['isAuth'] = false
    
    if (data['isAuth']) delete data['isAuth']
    else {
      console.log('CLOSE SESSION')
      data = null
    }
  }

  return data
}

const onFetch = async (params, method, token = 'DEFAULT', body = null) => {
  console.log('endpoint:', ENDPOINT + params)
  const pro = fetch(ENDPOINT + params, {
    method: method,
    headers: {
      'Content-Type': 'application/json',
      Authorization: token
    },
    body: body
  })
  return await processResponse(pro)
}

export const Context = React.createContext({
  onFetch
})